@use "../foundation" as *;

.cords__list {
  @include mq(md) {
    display: flex;
    flex-wrap: wrap;
  }
}

.cords-list {
}

.cords-list__item {
  @include mq(md) {
    max-width: calc(33.333% - #{rem(70)} * 2 / 3);
    width: 100%;
  }
}

.cords-list__item + .cords-list__item {
  margin-top: rem(30);
  @include mq(md) {
    margin-top: 0;
    margin-left: rem(70);
  }
}
.cords-list__item:nth-of-type(3n + 1) {
  @include mq(md) {
    margin-left: 0;
  }
}

.cords-list__item:nth-of-type(n + 4) {
  @include mq(md) {
    margin-top: rem(70);
  }
}

.cords-list__item a {
  display: block;
  height: 100%;
  background-color: #fff;
  box-shadow: 0 0 rem(40) 0 rgba(0, 0, 0, 0.05);
}

.cords-list__item-img {
}

.cords-list__item-img img {
  width: 100%;
  aspect-ratio: 382/210;
  object-fit: cover;
}

.cords-list__item-content {
  padding: rem(20) rem(25) rem(25);
  @include mq(md) {
    padding: 0 rem(30) rem(30);
  }
}

.cords-list__item-category {
  font-size: rem(12);
  font-weight: 500;
  line-height: 1;
  color: #aaa;
  @include mq(md) {
    font-size: rem(14);
  }
}

.cords-list__item-title {
  margin-top: rem(8);
  font-size: rem(18);
  font-weight: 700;
  line-height: 1.5;
  @include mq(md) {
    margin-top: rem(25);
    font-size: rem(20);
  }
}

.cords-list__item-text {
  margin-top: rem(10);
  font-size: rem(18);
  font-weight: 400;
  @include mq(md) {
    margin-top: rem(25);
  }
}
