@use '../foundation' as *;

.p-access__title {
  margin-bottom: rem(25);
}

.p-access__text {
  font-size:  rem(18);
  font-weight: 400;
  margin-bottom: rem(25);
}

.p-access__map {
  position: relative;
  width: 100vw;
  height: 0;
  left: 50%;
  transform: translateX(-50%);
  padding-top: per(420,365);
  @include mq(md){
    height: 0;
    padding-top: 47.5%;
  }
}

.p-access__map iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
