@use "../foundation" as *;

.p-mv {
  position: relative;
  z-index: 1;
  height: 30vh;
  min-height: rem(400);
  @include mq(md){
    height: 100vh;
  }
}

.l-mv {
}

.p-mv__inner {
  height: inherit;
  min-height: inherit;
}

.p-mv__title-wrap {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  color: $color-white;
  text-align: center;
}

.p-mv__main-title {
  font-size: rem(40);
  @include mq(md) {
    font-size: rem(60);
  }
}

.p-mv__sub-title {
  margin-top: rem(20);
  font-size: rem(20);
  @include mq(md) {
    font-size: rem(35);
  }
}

.p-mv__swiper,
.mv__swiper .swiper-img,
.mv__swiper .swiper-img img {
  height: inherit;
  min-height: inherit;
}

.p-mv__swiper .swiper-img img {
  width: 100%;
  object-fit: cover;
}

.p-mv__swiper .swiper-slide.swiper-slide-active .swiper-img,
.p-mv__swiper .swiper-slide.swiper-slide-duplicate-active .swiper-img,
.p-mv__swiper .swiper-slide.swiper-slide-prev .swiper-img {
  animation: scale 8s linear 0s normal both;
}

@keyframes scale {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.2);
  }
}

